var React = require('react'),
	url = require('url');

module.exports = React.createClass({
	displayName: "Image",

	getInitialState: function () {
		return {src: null};
	},

	getDefaultProps: function () {
		return {
			holderSrc: "holder.js/",
			holderSize: "200x200",
			holderText: "Loading..."
		}
	},

	render: function () {
		return (
			<img ref="image" src={this.state.src || this.props.holderSrc + this.props.holderSize} />
		);
	},

	componentDidMount: function () {
		require('holderjs').run({
			images: [this.refs.image.getDOMNode()]
		});
		if (this.props.src) {
			var url = this.props.src;
			if (url.indexOf("-original")) {
				url = this.props.src.replace("-original", "")
					.replace('.jpg', '_medium.jpg')
					.replace('.jpeg', '_medium.jpeg');
			}
			this.setState({src: url});
		}
	}


});